#Board {
  .toolbar{
    display: flex;
    width: 100%;
    .comp-overlay {
      position: absolute;
      z-index: 600;
      width: 100vw;
      height: 100vh;
      left: 0;
      top: 0;
      background-color: rgba(0,0,0,0.6);
    }
    .addNewContainer {
      display: flex;
      position: absolute;
      z-index: 640;
      background: white;
      width: 100vw;
      max-width: 860px;
      border-radius: 5px;
      top: 50%;
      left: 50%;
      /* bring your own prefixes */
      transform: translate(-50%, -50%);
      padding: 30px;
      .input-field {
        min-width: 500px;
      }
      .btn-container {
        margin: 1rem;
      }
    }
  }

  .board {
    padding: 0 30px;
    padding-top: 30px;
    display: flex;
    flex-wrap: wrap;
  }

  .trip-item {
    height: 200px;
    width: 500px;
    display: flex;
    background-color: white;
    margin-right: 30px;
    margin-bottom: 30px;

    .trip-item-icons {
      width: 50px;
      ul {
        margin: 0;
        padding: 0;
        li {
          list-style: none;
          width: 50px;
          height: 50px;
          text-align: center;
          border-bottom: 1px solid #efefef;
          padding: 12px 0;
          cursor: pointer;
          &:hover {
            background-color: #efefef;
          }
          &.disabled {
            cursor: auto;
            &:hover { background-color: white}
            a i {
              color: #999;
            }
          }
          a {
            i {
              color: #333;
            }
          }
        }
      }
    }
    .trip-item-img {
      height: 100%;
      width: 175px;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .trip-item-details {
      flex-grow: 1;
      padding: 15px;
      width: 325px;
      overflow: hidden;
      h3 {
        margin-top: 0;
        font-size: 2.5rem;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }

}